<template>
    <div class="item-wrap">
        <h1>商品一覧</h1>
        <div class="button-container">
            <button class="add-button" @click="add">追加する</button>
        </div>
        <div class="grid">
            <table id="itemLayout">
                <thead>
                    <tr>
                        <th>カテゴリー</th>
                        <th>商品名</th>
                        <th>商品画像</th>
                        <th>価格</th>
                        <th>表示</th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody v-if="merchandices.length > 0">
                    <tr class="item-row" v-for="(merchandice, value) in merchandices" :key="value" :id="value">
                        <td v-if="merchandice.category != null"><div class="category-box" :style="'background:' + merchandice.colorCode + ';'">{{ merchandice.category }}</div></td>
                        <td v-else><div class="category-box" style="background: #777;">未設定</div></td>
                        <td class="merchandice-name">{{ merchandice.itemName }}</td>
                        <td><img class="item-image" :src="'/' + merchandice.itemImage" alt=""></td>
                        <td>¥{{ merchandice.price }}</td>
                        <td v-if="merchandice.display"><button class="display-btn" @click="toggleDisplay(merchandice)">表示</button></td>
                        <td v-else><button class="display-btn hide" @click="toggleDisplay(merchandice)">非表示</button></td>
                        <td><i class="fa-lg fa-solid fa-pen-to-square edit-button" @click="edit(merchandice)"></i></td>
                        <td><i class="fa-lg fa-solid fa-trash delete-button" @click="removeItem(merchandice)"></i></td>
                    </tr>
                    <!-- データ行がここに挿入されます -->
                </tbody>
            </table>
        </div>
    </div>
</template>

<script>
import Ajax from '@/lib/Ajax';

// 3
export default {
    name: 'ItemList',
    data() {
        return {
            merchandices: []
        }
    },
    mounted() {
        this.getItems();
        this.$emit('setDisplay', 'items');
    },
    emits:[
        'setDisplay',
        'openPop',
        'setData'
    ],
    methods: {

        /**
         * 商品一覧を取得する処理
         * GET
         */
        getItems() {
            // Ajax('/assets/json/merchandice/item_list.json')
            Ajax('/api/master/merchandices')
            .then(res => res.json())
            .then(data => {
                this.merchandices = data;
            });
        },

        /**
         * ユーザー画面の商品表示を切り替える処理
         * PUT
         * @param {*} merch
         */
        toggleDisplay(merch) {
            const sendData = {
                display: merch.display
            };
            Ajax(`/api/master/merchandices/${merch.id}`, 'PUT', sendData);
            setTimeout(this.getItems, 100);
        },

        /**
         * 
         * DELETE
         * @param {*} e 
         */
        removeItem(merch) {
            Ajax(`/api/master/merchandices/${merch.id}`, 'DELETE');
            setTimeout(this.getItems, 100);
        },

        edit(item) {
            this.$emit('setData', item);
            this.$emit('openPop');
        },

        add() {
            this.$emit('setData', null);
            this.$emit('openPop');
        }
    }
}
</script>
